import React, { Component } from "react";
import { withRouter } from 'react-router';

class EmployeeView extends Component {
  employee = {
    Name: "Aurora",
    Age: 53,
    Email: "ante.blandit@disparturient.ca",
    "Date of joining": "10/06/2019",
    "Phone number": "1628101902299",
    Street: "493 Iaculis Rd.",
    City: "Lewiston",
    Zip: "42591-180",
    Region: "Maine",
    Country: "Sudan",
    Info: "est. Nunc ullamcorper, velit in aliquet lobortis, nisi nibh lacinia",
    EmployeeID: 101
  };
  constructor(props) {
    super(props);
    this.props = props;
  }

  back(){
    // this.props.history.push('/employeeList');
    this.context.router.push('/employeeList');
  }

  generateEachRow() {
    let eachDataKeys = Object.keys(this.employee);
    let eachData = eachDataKeys.map(eachKey => {
      return (
        <tr key={eachKey}>
          <td>{eachKey}</td>
          <td>{this.employee[eachKey]}</td>
        </tr>
      );
    });
    return eachData;
  }

  render() {
    let eachDetail = this.generateEachRow();
    return (
      <div className="viewEmployee">
        <div className="row">
          <div className="col-md-3">
            <div className="back">
              <button className="btn btn-danger" onClick={this.back}>Back</button>
            </div>
            <img src="./dummy.jpg" alt="Image path wrog" />
          </div>
          <div className="col-md-9">
            <table className="table table-hover">
              <tbody>
                  {eachDetail}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(EmployeeView);
