import React from "react";
// import logo from './logo.svg';
import "./App.css";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import Home from "./components/Home";
import Secret from "./components/Secret";
import Login from "./components/Login";
import Pagination from "./components/Pagination";
import Chart from './components/Chart';
import EmployeeList from './components/EmployeeList';
import EmployeeView from './components/EmployeeView';
import NotFound from './components/pageNotFound';

function App() {
  return (
    <Router>
      <div>
        <nav className="navbar navbar-expand-sm bg-light navbar-light">
          <ul className="navbar-nav">
            <li className="nav-item">
              <Link className="nav-link" to="/">Home</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/secret">Secret</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/login">login</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/pagination">Pagination</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/chart">chart</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/employeeList">EmployeeList</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="/employeeView">EmployeeView</Link>
            </li>
          </ul>
        </nav>
        <Switch>
          <Route path="/" exact component={Home} />
          <Route path="/secret" component={Secret} />
          <Route path="/login" component={Login} />
          <Route path="/pagination" component={Pagination} />
          <Route path="/chart" component={Chart} />
          <Route path="/employeeList" component={EmployeeList} />
          <Route path="/employeeView" component={EmployeeView} />
          <Route path="*" component={NotFound} />
        </Switch>
      </div>
    </Router>
  );
}

