import React from "react";
import "./Table.css";

class Table extends React.Component {
  data = [];
  headings = [];
  pageData = [];
  lastPage = 0;
  pageButton = [];

  constructor(props) {
    super(props);
    // this.props = props;
    this.state = {
      message: "Loading...",
      page: 0,
      pageLimit: 10,
      initial: 0,
      end: 10
    };
    this.data = this.props.data;
    this.headings = this.props.headings;
    this.findLastPage();
    this.setPage(0);
    this.changePageLimit = this.changePageLimit.bind(this);
  }

  findLastPage() {
    let lastPage = Math.ceil(this.data.length / this.state.pageLimit) - 1;
    this.lastPage = lastPage > 0 ? lastPage : 0;
  }

  setPage(page, pageLimit = this.state.pageLimit) {
    this.pageData = [];
    let initial = page * pageLimit;
    let end = initial + pageLimit;
    for (let i = initial; i < end; i++) {
      this.pageData[i] = this.data[i];
    }
    this.pageButton = this.generatePageButtons(page);
    this.setState({ page: page });
  }

  generatePageButtons(page) {
    let pageButtons = [];
    for (let i = page - 2; i <= page + 2; i++) {
      if (i >= 0 && i <= this.lastPage) pageButtons.push(i);
    }
    return pageButtons;
  }

  changePageLimit(event) {
    let limit = parseInt(event.target.value);
    this.setState({ pageLimit: limit });
    this.setPage(0,limit );
  }

  render() {
    let headingDisplayData = this.headings.map(eachData => {
      return <th>{String(eachData.toUpperCase())}</th>;
    });
    let displayData = this.pageData.map(eachData => {
      let eachNode = [];
      for (let node of this.headings) {
        if (eachData[node]) {
          // eachNode += <td>{eachData[node]}</td>;
          eachNode.push(<td>{eachData[node]}</td>);
        } else {
          eachNode.push(<td></td>);
        }
      }
      // eachNode += </tr>;
      return <tr>{eachNode}</tr>;
    });
    let dispPageButton = this.pageButton.map(eachData => {
      if (eachData !== this.state.page) {
        return (
          <button
            className="btn btn-danger"
            onClick={() => this.setPage(eachData)}
          >
            {eachData + 1}
          </button>
        );
      } else {
        return (
          <button
            className="btn btn-primary"
            onClick={() => this.setPage(eachData)}
          >
            {eachData + 1}
          </button>
        );
      }
    });

    return (
      <div className="table">
        <div className="controls">
          <button
            disabled={this.state.page === 0}
            onClick={() => this.setPage(0)}
            className="btn btn-danger"
          >
            &lt;&lt;
          </button>
          <button
            disabled={this.state.page === 0}
            onClick={() => this.setPage(this.state.page - 1)}
            className="btn btn-danger"
          >
            &lt;
          </button>
          {dispPageButton}
          <button
            disabled={this.state.page === this.lastPage}
            onClick={() => this.setPage(this.state.page + 1)}
            className="btn btn-danger"
          >
            &gt;
          </button>
          <button
            disabled={this.state.page === this.lastPage}
            onClick={() => this.setPage(this.lastPage)}
            className="btn btn-danger"
          >
            &gt;&gt;
          </button>
          <select value={this.state.pageLimit} onChange={this.changePageLimit}>
            <option value="10">10</option>
            <option value="15">15</option>
            <option value="20">20</option>
            <option value="25">25</option>
          </select>
        </div>
        <table className="table table-hover">
          <thead>
            <tr>{headingDisplayData}</tr>
          </thead>
          <tbody>{displayData}</tbody>
        </table>
      </div>
    );
  }
}

export default Table;
